body{
    margin: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.divBody{
    width: 100%;
    height: 100%;
    position: absolute;
}
.divContent{
    width: 100%;    
    height: 100%;
}
.divLeftContainer{
    height: 100%;
   width: 40%;
    float: left;
    background-color: #3C89C7;  
}
.divRightContainer{
    height: 100%;
    width: 60%;
    float: left;
    background-color: #fff;
}
.divLoginBox{
    border-radius: 15px;
    position: relative;
    width: 60%;
    height: 40%;
    padding: 15px;
    background-color: #fff;
    box-shadow: 6px 6px 24px 18px #ccc;
    left: 10%;
    top: 25%;
}
.divLoginBox-title{
    width: calc(100% - 20px);
    height: 40px;
    padding: 10px;
}
.spnLoginBox-title{
    line-height: 35px;
    vertical-align: middle;
}
.divLoginTitle{
    width: 50%;
    height: 100%;
    float: left;
    text-align: center;
    cursor: pointer;
}
.divRegisterTitle{
    width: 50%;
    height: 100%;
    float: right;
    text-align: center;
    cursor: pointer;
}
.active{
    background-color: #3C89C7;
    color: #fff;
}
.divInputdetails{
    width: calc(100% - 20px);
    height: 50%;
    padding: 10px;
}
.inputText{
    width: calc(100% - 35px);
    font-size: 15px;
    border-radius: 5px;
    padding: 15px;
    border-color: #000;
    margin-top: 30px;
}
.divButtons{
    margin-top: 10px;
    height: 20%;
    text-align: center;
    padding: 10px;
}
.buttonLogin{
    padding: 10px;
    border-radius: 20px;
    width: 100px;
    border: none;
    color: #fff;
    background-color: #019267;
    cursor: pointer;
}
